  grid-gap: 1rem;
  max-width: 1300px;
  margin: 0 auto; }

input, button {
  padding: 0.4rem;
  outline: none; }

button {
  background-color: #7564D5;
  border-color: #7564D5;
  color: #fff; }

.App {
  display: grid;
  grid-template-areas: "header header header" "search search search" "content content content" "footer  footer  footer"; }

.Header {
  grid-area: header;
  background-color: #44A799;
  color: #fff;
  padding: 1rem; }

.SearchBar {
  grid-area: search;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin-top: 2rem; }
  .SearchBar input {
    margin-right: 4px; }

.Dashboard {
  grid-area: content;
  padding: 50px 10px;
  text-align: center; }
  .Dashboard h2 {
    font-weight: lighter;
    font-size: 2rem; }

.Footer {
  grid-area: footer;
  background-color: #3e3e3e;
  color: #fff;
  padding: 2rem;
  text-align: center; }

.Card {
  padding: 1.2rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  color: #fff; }
  .Card--green {
    background-color: #D7F2ED;
    color: #1B6F71; }
    .Card--green h2 {
      color: #1B6F71; }
    .Card--green li {
      border-left-color: #1B6F71; }
  .Card--pink {
    background-color: #EB6D93;
    color: #f7dedf; }
    .Card--pink h2 {
      color: #f7dedf; }
    .Card--pink li {
      border-left-color: #f7dedf; }
  .Card--purple {
    background-color: #7564D5;
    color: #fff; }
    .Card--purple h2 {
      color: #fff; }
    .Card--purple li {
      border-left-color: #fff; }
  .Card h2 {
    font-weight: lighter;
    margin-bottom: 2.8rem; }
  .Card h3 {
    font-size: 4rem;
    line-height: 5rem;
    margin: 0; }
  .Card ul {
    list-style: none;
    padding: 0;
    margin: 0; }
  .Card li {
    padding: 0;
    padding-left: 1rem;
    margin-bottom: 1rem;
    text-align: left;
    border-left-width: 4px;
    border-left-style: solid; }



